import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Popover from "@mui/material/Popover";

const dispatchedEvents = [];

const SrsMuiPopover = function (props, ref) {
  return (
    <Popover
      {...props}
      theme={props.muiTheme}
    >
      <slot />
    </Popover>
  );
};

const styles = `
  /* Custom styles for the MuiPopover component */
`;

const props = [
  {
    name: "open",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "anchorEl",
    defaultValue: null,
    type: "object",
  },
  {
    name: "anchorOrigin",
    defaultValue: {
      vertical: "bottom",
      horizontal: "left",
    },
    type: "object",
  },
  {
    name: "transformOrigin",
    defaultValue: {
      vertical: "top",
      horizontal: "left",
    },
    type: "object",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to Popover if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiPopover),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
